.header{
    position: fixed;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
    background-color: rgba(0,0,0,.9);
}

.header .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 1rem;
    background-color: transparent;
}

.header .nav-menu a {
    color: #fff;
}

.header .navbar img {
    width: 48 px;
    height: 48px;
    border-radius: 24px 0 24px 0;
}

.header .nav-menu {
    display: flex;
}

.header .nav-item {
    padding: 1rem;
    font-weight: 500;
}

.nav-item .active {
    padding-bottom: 12px;
    border-bottom: 3px solid var(--secondary-color);
}

.header .nav-item a:hover {
    padding-bottom: 12px;
    border-bottom: 3px solid var(--secondary-color);
}

.hamburger {
    display: none;
}









@media screen and (max-width:940px) {
    .header {
        max-width: 100%;
        padding: 1rem;
        background-color: rgba(0,0,0,0.9);
    }
    
    .header .navbar {
        max-width: 100%;
        background-color: transparent;
    }
    
    .hamburger {
        display: block;
        color: #fff;
    }

    .nav-menu{
        position: fixed;
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgba(0,0,0,.9);
        width: 100%;
        height: 90vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem 0;
    }

    .header .navbar img {
        width: 48px;
        border-radius: 24px 0 24px 0 ;
    }
}





